import { Component, OnInit, OnChanges, Input  } from '@angular/core';
import { FormGroup, FormControl, FormBuilder, Validators, FormArray } from '@angular/forms';
import { AddressbookComponent } from '../addressbook/addressbook.component';
import { ContactsComponent } from '../contacts/contacts.component';
import { ContactDetailComponent } from '../contacts/contact-detail.component';
// import { mailValidator } from '.././shared/mailValidator';
// import { patternValidator } from '../shared/pattern-validator';
// import { ContactsComponent } from '../contacts/contacts.component';
import { ContactService } from '../shared/contact.service';
import { AddRoutes } from './add.routes';
// import { SearchPipe } from '../contacts/search.pipe';
import { RouterModule, Routes } from '@angular/router';
import { routing } from 'app.routes';

@Component( {
  selector: 'app-add',
  templateUrl: './add.component.html',
  styleUrls: ['./add.component.css'],
})

  export class AddComponent implements OnInit {


//@Input() contacts: Object;

    myForm: FormGroup;
 //submitted: boolean;
 //active = true;

  constructor ( private ContactService: ContactService, private fb: FormBuilder ) {};

//  addContact(_newContact: any) {
//   this.ContactService.addContact(_newContact);
 //};

    ngOnInit() {
        this.myForm = this.fb.group ( {
     'id': [null, [<any> Validators.required, <any> Validators.minLength(1),<any> Validators.maxLength(2) ]],
     'img': '',
     'firstname': [null, [<any> Validators.required, <any>Validators.minLength(3)]],
     'lastname': [null, [<any> Validators.required, <any>Validators.minLength(3)]],
     'phone': [null, [<any> Validators.required, <any>Validators.minLength(10)]],
     'job_phone': [null, [<any> Validators.required, <any>Validators.minLength(10)]],
     'job_title': [null, [<any> Validators.required, <any>Validators.minLength(1)]],
     'company': [null, [<any> Validators.required, <any>Validators.minLength(3)]],
     'email': [null, [<any>Validators.required, <any>Validators.email /*(/^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/)*/]],
   });
 }

    onSubmitForm() {
       this.ContactService.addContact(this.myForm.value);
  };
}



