import { BrowserModule } from '@angular/platform-browser';
import { NgModule, Component } from '@angular/core';
import { HttpModule } from '@angular/http';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { routing } from '../app.routes';
import { RouterModule, Routes } from '@angular/router';
import { ContactFilterPipe } from './shared/filter.pipe';
import { ContactService } from './shared/contact.service';
import { AppComponent } from './app.component';
import { AddressbookComponent } from './addressbook/addressbook.component';
import { ContactsComponent } from './contacts/contacts.component';
import { ContactDetailComponent } from './contacts/contact-detail.component';
import { AddComponent } from './add/add.component';
// import { mailValidator } from './shared/mailValidator';
// import { AddRoutes } from './add/add.routes';

@NgModule({
  declarations: [
    AppComponent,
    AddressbookComponent,
    ContactsComponent,
    AddComponent,
  //  AddRoutes,
    ContactDetailComponent,
    ContactFilterPipe
  ],
  imports: [
    BrowserModule,
    FormsModule,
    HttpModule,
    ReactiveFormsModule,
    // mailValidator,
    routing
  ],
  providers: [ContactService, ContactFilterPipe],
  bootstrap: [AppComponent]
})
export class AppModule { }



