import { NgModule, ModuleWithProviders } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import {FormGroup, FormControl, FormBuilder, Validators, FormArray} from "@angular/forms";
// import { SearchPipe } from '../src/app/contacts/search.pipe';
import { ContactService } from '../src/app/shared/contact.service';
import { AddressbookComponent } from '../src/app/addressbook/addressbook.component';
import { ContactsComponent } from '../src/app/contacts/contacts.component';
import { ContactDetailComponent } from '../src/app/contacts/contact-detail.component';
import { AddComponent } from '../src/app/add/add.component';

const routes: Routes = [
  { path: 'contacts', component: ContactsComponent },
  { path: 'addressbook', component: AddressbookComponent },
  { path: 'contact/:id', component: ContactDetailComponent },
  { path: '', component: AddressbookComponent },
  { path: 'new', component: AddComponent }
]

NgModule({
 imports: [ RouterModule.forRoot(routes) ],
 exports: [ RouterModule ]
 })
export const routing: ModuleWithProviders = RouterModule.forRoot(routes);



